.main-nav {
  min-height: 65px;
}

.main-nav__list {
  padding: 0;
  list-style: none;
}

/*.main-nav--nojs .burger {
  display: none;
}*/

.main-nav--closed {
  display: none;
}

.main-nav--closed .site-list {
  display: none;
}

.main-nav--opened .site-list {
  display: flex;
}

@media (min-width: @tablet-width) {
  .main-nav--closed {
    display: flex;
    z-index: 300;
  }

  .main-nav--closed .site-list {
    display: flex;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    width: 480px;
  }
}
