body {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  min-width: 320px;
  margin: 0;
  padding: 0;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
.button {
  display: block;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  padding: 11px 0;
  color: #ffffff;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--header {
  margin-top: 2px;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 1300px) {
  .button--header {
    transform: translateY(4px);
  }
}
.catalog__title {
  font-size: 36px;
  line-height: 60px;
  position: relative;
  font-weight: normal;
  margin-top: 15px;
}
.catalog__list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
}
.catalog__item {
  margin-bottom: 21px;
  position: relative;
}
.catalog__item::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -20px;
  width: 320px;
  height: 1px;
  background-color: #ededed;
}
.catalog-card {
  display: flex;
  flex-wrap: wrap;
  /*align-items: center;*/
}
.catalog-card__link {
  text-decoration: none;
  color: inherit;
}
.catalog-card--more br {
  display: none;
}
.catalog-card__right-column {
  text-align: right;
}
.catalog-card--more {
  padding-top: 25px;
  justify-content: center;
}
.catalog-card__table {
  border-collapse: collapse;
  width: 140px;
  margin-top: 19px;
}
.catalog-card__table br {
  display: none;
}
.catalog-card__title {
  text-transform: uppercase;
  width: 75px;
  text-align: left;
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 12px;
}
.catalog-card__img {
  width: 140px;
  height: 144px;
}
.catalog-card__image {
  background: transparent;
}
.catalog-card__table td {
  width: 50%;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}
.catalog-card__show {
  position: relative;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  padding-top: 53px;
}
.catalog-card__show::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 30px;
  height: 30px;
  margin-left: -15px;
  background-image: url("../img/icon-plus-mobile.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}
.catalog-card__text {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  margin-bottom: 20px;
  transform: translateY(-5px);
}
.catalog-card__btn {
  width: 100%;
  text-align: center;
  padding: 10px 0;
  margin-bottom: 5px;
  margin-top: -1px;
}
.catalog-card__btn--more {
  background-color: #f2f2f2;
  color: #444444;
}
.catalog-card__btn--more:hover {
  background-color: #ebebeb;
}
.catalog-card__btn--more:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  tbody {
    width: 100%;
  }
  .catalog__title {
    margin-bottom: 166px;
    margin-top: 54px;
    font-size: 60px;
    line-height: 60px;
  }
  .catalog__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 0;
  }
  .catalog__item {
    box-sizing: border-box;
    min-height: 430px;
    width: 324px;
    margin-right: 50px;
    background-color: #f2f2f2;
    padding: 0 37px;
    border: none;
    margin-bottom: 140px;
  }
  .catalog__item:nth-child(2n) {
    margin-right: 0;
  }
  .catalog__item:last-child {
    background-color: #ffffff;
    border: 2px solid #e6e6e6;
  }
  .catalog__item::before {
    display: none;
  }
  .catalog-card {
    flex-direction: column;
    /*transform: translateX(-17px);*/
  }
  .catalog-card--more {
    /*padding-top: 50px;*/
    text-align: center;
  }
  .catalog-card__show {
    font-size: 20px;
    padding-top: 155px;
    margin-bottom: 21px;
    transform: translate(1px, -15px);
  }
  .catalog-card__show::before {
    top: 50%;
    width: 60px;
    height: 60px;
    margin-left: -31px;
    margin-top: -52px;
    background-image: url("../img/icon-plus.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
  }
  .catalog-card__text {
    margin: 0;
    font-size: 16px;
    margin-bottom: 80px;
    transform: translate(1px, -9px);
  }
  .catalog-card__title {
    width: 100%;
    text-align: center;
    font-size: 20px;
    margin-top: 6px;
    margin-bottom: 18px;
  }
  .catalog-card__title br,
  .catalog-card--more br {
    display: block;
  }
  .catalog-card__table {
    width: 100%;
    margin-bottom: 30px;
    margin-top: 0;
  }
  .catalog-card__table tr {
    border-bottom: 1px solid #cdcdcd;
  }
  .catalog-card__table td {
    padding: 8px 0;
    font-size: 16px;
  }
  .catalog-card__img {
    width: 320px;
    height: 174px;
    margin-left: -55px;
    margin-top: -45px;
    margin-bottom: 30px;
    transform: translateX(18px);
  }
  .catalog-card__img--big {
    height: 208px;
    margin-top: -80px;
  }
  .catalog-card__btn {
    margin-bottom: 0;
  }
  /*.catalog-card__btn--more {
    transform: translateX(19px);
  }*/
}
@media (min-width: 1300px) {
  .catalog__item {
    width: 250px;
    margin-right: 0;
    margin-bottom: 145px;
  }
  .catalog__item:nth-child(2n) {
    margin-right: 0;
  }
  .catalog-card__img {
    width: 245px;
  }
  .catalog__title {
    margin-top: 70px;
  }
  .catalog-card__btn {
    width: 170px;
  }
}
