.example {
  padding: 15px 0 30px;
  /*background: linear-gradient(to right, #f2f2f2 49%, #eaeaea 49%);*/
  background-color: #f2f2f2;
}
.example__tabs {
  margin: 0 auto;
}
.example__image {
  width: 320px;
  height: 240px;
  display: block;
  margin-top: 35px;
  margin-bottom: 33px;
  margin-left: -60px;
}
.example__image--after {
  display: none;
}
.example__title {
  font-weight: normal;
  font-size: 36px;
  line-height: 60px;
  margin: 0;
  margin-bottom: 23px;
}
.example__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin: 0;
  margin-bottom: 23px;
  max-width: 270px;
}
.example__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.example__results {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.example__result {
  position: relative;
  box-sizing: border-box;
  padding-top: 16px;
  width: 125px;
  text-align: center;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  min-height: 56px;
  /*padding: 16px 0;*/
  border: 1px solid #cdcdcd;
  border-radius: 5px;
}
.example__total {
  position: absolute;
  width: 88px;
  bottom: -18px;
  left: 18px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-transform: none;
  color: #444444;
  background-color: #f2f2f2;
  vertical-align: bottom;
}
.example__cost {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0 auto;
  margin-top: 37px;
}
.example__switcher {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 200px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.example__slider {
  box-sizing: border-box;
  position: relative;
  width: 85px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  background-color: #ffffff;
}
.example__slider span {
  position: absolute;
  display: block;
  top: 50%;
  left: 5px;
  width: 35px;
  height: 6px;
  background-color: #68b738;
  border-radius: 5px;
  margin-top: -3px;
  transition: 0.4s;
}
.example__toggle--before {
  transform: translateX(-8px);
}
.example__switcher input {
  display: none;
}
.example__switcher input:checked + .example__slider span {
  transform: translateX(39px);
}
.example__switcher input:checked ~ .example__image--before {
  display: none;
}
.example__switcher input:checked ~ .example__image--after {
  display: block;
}
.example__comparison {
  display: none;
  padding-bottom: 20px;
  margin-top: 250px;
  /*background: linear-gradient(to right, #f2f2f2 49%, #eaeaea 49%);*/
}
.example__photos {
  position: relative;
  width: 708px;
  min-height: 460px;
  margin: 0 auto;
}
.example__photo-before,
.example__photo-after {
  position: absolute;
  top: -170px;
  left: 10px;
}
.example__photo-before {
  z-index: 20;
  clip: rect(auto, 345px, auto, auto);
}
.example__photo-after {
  clip: rect(auto, auto, auto, 345px);
}
.example__range {
  position: absolute;
  top: 372px;
  width: 570px;
  left: 50%;
  margin-left: -285px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.example__scale {
  position: relative;
  width: 425px;
  height: 6px;
  background-color: #dcdcdc;
  border-radius: 5px;
}
.example__toggle {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -17px;
  margin-left: -17px;
  width: 34px;
  height: 34px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);
  transition: 0.3s;
}
.example__toggle:hover {
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25);
}
.example__toggle:active {
  border-color: #68b738;
}
.example__toggle::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 50%;
  left: 50%;
  margin-top: -4px;
  margin-left: -4px;
  background-color: #68b738;
  border-radius: 50%;
}
.example__button {
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  border-bottom: 1px dashed #bebebe;
  padding: 0;
  padding-bottom: 6px;
}
